import React, { useState} from 'react';
import Menu from './Menu'; 



const scrollToDescription = () => {

    document.querySelector('.DescContainerMain').scrollIntoView({ behavior: 'smooth' });
};

const LoanDefault = ({ backgroundImage }) => {
  const [age, setAge] = useState('');
  const [income, setIncome] = useState('');
  const [loanAmount, setLoanAmount] = useState('');
  const [creditScore, setCreditScore] = useState('');
  const [monthsEmployed, setMonthsEmployed] = useState('');
  const [numCreditLines, setNumCreditLines] = useState('');
  const [interestRate, setInterestRate] = useState('');
  const [loanTerm, setLoanTerm] = useState('');
  const [dtiRatio, setDtiRatio] = useState('');
  const [education, setEducation] = useState('');
  const [employmentType, setEmploymentType] = useState('');
  const [maritalStatus, setMaritalStatus] = useState('');
  const [hasMortgage, setHasMortgage] = useState('');
  const [hasDependents, setHasDependents] = useState('');
  const [loanPurpose, setLoanPurpose] = useState('');
  const [hasCoSigner, setHasCoSigner] = useState('');

  const [predictedDefault, setPredictedDefault] = useState(null);
  const [confidencePercentage, setconfidencePercentage] = useState(null);
  const executePrediction = async () => {
    if (!age || !income || !loanAmount || !creditScore || !monthsEmployed || !numCreditLines || !interestRate || !loanTerm || !dtiRatio || !education || !employmentType || !maritalStatus || !hasMortgage || !hasDependents || !loanPurpose || !hasCoSigner) return;

      try {
        const payload = {
          Age: parseFloat(age),
          Income: parseFloat(income),
          LoanAmount: parseFloat(loanAmount),
          CreditScore: parseInt(creditScore, 10),
          MonthsEmployed: parseInt(monthsEmployed, 10),
          NumCreditLines: parseInt(numCreditLines, 10),
          InterestRate: parseFloat(interestRate),
          LoanTerm: parseInt(loanTerm, 10),
          DTIRatio: parseFloat(dtiRatio),
          Education: education,
          EmploymentType: employmentType,
          MaritalStatus: maritalStatus,
          HasMortgage: hasMortgage ? 'Yes' : 'No',
          HasDependents: hasDependents ? 'Yes' : 'No',
          LoanPurpose: loanPurpose,
          HasCoSigner: hasCoSigner ? 'Yes' : 'No'
        };
          
          const response = await fetch(`${process.env.REACT_APP_API_URL}/predict_loan_default`, {
              method: 'POST',
              headers: {
                  'Content-Type': 'application/json',
              },
              body: JSON.stringify(payload),
          });

          const data = await response.json();
          console.log(data); // This will output the API response to the console

          setPredictedDefault(data.predictedDefault);
          setconfidencePercentage(data.confidencePercentage);
      } catch (error) {
          console.error("Erreur lors de la demande de prédiction:", error);
      }
  };
    return (
      <body>
        <div className="body">
          <div className="BGImageContainer" style={{ backgroundImage: `url(${backgroundImage})` }}>
            <div>
              <Menu />
            </div>
            <div className="FirstContainer">
              <div className='CntFirstContainer'>
                <div className="PanelContainer">
                  <div className="GoldPredictionResultContainer">
                    {predictedDefault !== null ? (
                      <>
                        <div><h3>Predicted Default Risk: {predictedDefault ? 'High' : 'Low'}</h3></div>
                        {confidencePercentage && (
                          <div><h4>Confidence: {confidencePercentage }</h4></div> // Format the confidence as a percentage
                        )}
                      </>
                    ) : (
                      <div className="placeholder">No Prediction Available Yet</div>
                    )}
                  </div>
                  <div className="LoanDefaultInputsContainer">
                    {/* Age */}
                    <div className="LoanDefaultFormElement">
                      <label htmlFor="age">Age</label>
                      <input type="number" value={age} onChange={(e) => setAge(e.target.value)} placeholder="25" id="age" />
                    </div>
                    {/* Income */}
                    <div className="LoanDefaultFormElement">
                      <label htmlFor="income">Annual income</label>
                      <input type="number" value={income} onChange={(e) => setIncome(e.target.value)} placeholder="100000" id="income" />
                    </div>
                    {/* Loan Amount */}
                    <div className="LoanDefaultFormElement">
                      <label htmlFor="loanAmount">Loan Amount</label>
                      <input type="number" value={loanAmount} onChange={(e) => setLoanAmount(e.target.value)} placeholder="25000" id="loanAmount" />
                    </div>
                    {/* Credit Score */}
                    <div className="LoanDefaultFormElement">
                      <label htmlFor="creditScore">Credit Score</label>
                      <input type="number" value={creditScore} onChange={(e) => setCreditScore(e.target.value)} placeholder="0 ≤ x ≤ 1000" id="creditScore" />
                    </div>
                    {/* Months Employed */}
                    <div className="LoanDefaultFormElement">
                      <label htmlFor="monthsEmployed">Months Employed</label>
                      <input type="number" value={monthsEmployed} onChange={(e) => setMonthsEmployed(e.target.value)} placeholder="80" id="monthsEmployed" />
                    </div>
                    {/* Number of Credit Lines */}
                    <div className="LoanDefaultFormElement">
                      <label htmlFor="numCreditLines">Number of Credit Lines</label>
                      <input type="number" value={numCreditLines} onChange={(e) => setNumCreditLines(e.target.value)} placeholder="3" id="numCreditLines" />
                    </div>
                    {/* Interest Rate */}
                    <div className="LoanDefaultFormElement">
                      <label htmlFor="interestRate">Interest Rate</label>
                      <input type="number" value={interestRate} onChange={(e) => setInterestRate(e.target.value)} placeholder="10" id="interestRate" />
                    </div>
                    {/* Loan Term */}
                    <div className="LoanDefaultFormElement">
                      <label htmlFor="loanTerm">Loan Term</label>
                      <input type="number" value={loanTerm} onChange={(e) => setLoanTerm(e.target.value)} placeholder="36" id="loanTerm" />
                    </div>
                    {/* DTI Ratio */}
                    <div className="LoanDefaultFormElement">
                      <label htmlFor="dtiRatio">DTI Ratio</label>
                      <input type="number" value={dtiRatio} onChange={(e) => setDtiRatio(e.target.value)} placeholder="25" id="dtiRatio" />
                    </div>
                    {/* Education */}
                    <div className="LoanDefaultFormBoxElement">
                      <label htmlFor="education">Education</label>
                      <select value={education} onChange={(e) => setEducation(e.target.value)} id="education">
                        <option value="">Select Education</option>
                        <option value="High School">High School</option>
                        <option value="Bachelor's">Bachelor's</option>
                        <option value="Master's">Master's</option>
                        <option value="PhD">PhD</option>
                      </select>
                    </div>

                    {/* Employment Type */}
                    <div className="LoanDefaultFormBoxElement">
                      <label htmlFor="employmentType">Employment Type</label>
                      <select value={employmentType} onChange={(e) => setEmploymentType(e.target.value)} id="employmentType">
                        <option value="">Select Employment Type</option>
                        <option value="Unemployed">Unemployed</option>
                        <option value="Part-time">Part-time</option>
                        <option value="Self-employed">Self-employed</option>
                        <option value="Full-time">Full-time</option>
                      </select>
                    </div>

                    {/* Marital Status */}
                    <div className="LoanDefaultFormBoxElement">
                      <label htmlFor="maritalStatus">Marital Status</label>
                      <select value={maritalStatus} onChange={(e) => setMaritalStatus(e.target.value)} id="maritalStatus">
                        <option value="">Select Marital Status</option>
                        <option value="Single">Single</option>
                        <option value="Married">Married</option>
                        <option value="Divorced">Divorced</option>
                      </select>
                    </div>

                    {/* Has Mortgage */}
                    <div className="LoanDefaultFormBoxElement">
                      <label htmlFor="hasMortgage">Has Mortgage</label>
                      <select value={hasMortgage} onChange={(e) => setHasMortgage(e.target.value)} id="hasMortgage">
                        <option value="">Select Option</option>
                        <option value="Yes">Yes</option>
                        <option value="No">No</option>
                      </select>
                    </div>

                    {/* Has Dependents */}
                    <div className="LoanDefaultFormBoxElement">
                      <label htmlFor="hasDependents">Has Dependents</label>
                      <select value={hasDependents} onChange={(e) => setHasDependents(e.target.value)} id="hasDependents">
                        <option value="">Select Option</option>
                        <option value="Yes">Yes</option>
                        <option value="No">No</option>
                      </select>
                    </div>

                    {/* Loan Purpose */}
                    <div className="LoanDefaultFormBoxElement">
                      <label htmlFor="loanPurpose">Loan Purpose</label>
                      <select value={loanPurpose} onChange={(e) => setLoanPurpose(e.target.value)} id="loanPurpose">
                        <option value="">Select Loan Purpose</option>
                        <option value="Personal">Personal</option>
                        <option value="Business">Business</option>
                        <option value="Education">Education</option>
                        <option value="Home">Home</option>
                        <option value="Other">Other</option>
                      </select>
                    </div>

                    {/* Has CoSigner */}
                    <div className="LoanDefaultFormBoxElement">
                      <label htmlFor="hasCoSigner">Has CoSigner</label>
                      <select value={hasCoSigner} onChange={(e) => setHasCoSigner(e.target.value)} id="hasCoSigner">
                        <option value="">Select Option</option>
                        <option value="Yes">Yes</option>
                        <option value="No">No</option>
                      </select>
                    </div>
                  </div>
                  <div className="button-container">
                    <button className="gold-buttonLD" onClick={executePrediction}>
                      Predict Closing Price
                    </button>
                  </div>  
                </div>
                <div className="DescRefContainer" onClick={scrollToDescription}>
                  <p className="Clickable-text">Learn more about this project</p>
                  <img src={`${process.env.PUBLIC_URL}/DescLogo.png`} alt="Logo" className="Clickable-logo" />
                </div>
              </div>
            </div>
          </div>
          <div className="DescContainerMain">
            <div className="DescContainer">
              <h6>Description</h6>
              <h5>This project was created using a Kaggle dataset to simulate a virtual asset manager helper. The purpose of this model is to predict the price of Gold using little information. </h5>
            </div>
          </div>
        </div>
      </body> 
    );
  };

export default LoanDefault;